import React from 'react'
import { db } from "../../util/firebase";
import './updateButton.css'

class UpdateButton extends React.Component {
    constructor(props) {
        super(props)
        
        this.updateData = this.updateData.bind(this)
    }

    updateData() {
        const wall = this.props.materials.wall
        const roof = this.props.materials.roof
        const floor = this.props.materials.floor
        const foundation = this.props.materials.foundation

            return db
            .ref(`${this.props.building.address}/materials`)
            .update(
                {wall,roof,floor,foundation},
            )
            .then(_ => {
                this.setState({ wall: this.props.materials.wall,
                                roof: this.props.materials.roof,
                                floor: this.props.materials.floor,
                                foundation: this.props.materials.foundation});
            });
      }

    render() {
    if (this.props.materials.wall.length === 0 || this.props.materials.roof.length === 0 || this.props.materials.floor.length === 0 || this.props.materials.foundation.length === 0) {
            return(
                <div></div>
            )
        } else {
            return (
            <div className='buttonDivUpdate'>
                <a className='buttonUpdate' onClick={this.updateData}>Gem</a>
            </div>
            )
        }
    }
}

export default UpdateButton