import React from 'react'
import './ResultsSimple.css'
import { Bar } from 'react-chartjs-2';

class ResultsSimple extends React.Component {
    constructor(props) {
        super(props)

        this.sumMaterials = this.sumMaterials.bind(this)
        this.removeMissing = this.removeMissing.bind(this)
        this.getLabels = this.getLabels.bind(this)
        this.getData = this.getData.bind(this)
    }

    sumMaterials(array) {
        const newArray = []
        const Beton = ['Beton',0]
        const Træ = ['Træ',0]
        const Skifer = ['Skifer',0]
        const Tegl = ['Tegl',0]
        const Isolering = ['Isolering',0]
        const Pap = ['Pap',0]
        const Mursten = ['Mursten',0]

        for (var x of array) {
            if (x[0] === 'Træ') {
                Træ[1] = Træ[1]+x[1]
            }
            if (x[0] === 'Beton') {
                Beton[1] = Beton[1]+x[1]
            }
            if (x[0] === 'Skifer') {
                Skifer[1] = Skifer[1]+x[1]
            }
            if (x[0] === 'Tegl') {
                Tegl[1] = Tegl[1]+x[1]
            }
            if (x[0] === 'Isolering') {
                Isolering[1] = Isolering[1]+x[1]
            }
            if (x[0] === 'Pap') {
                Pap[1] = Pap[1]+x[1]
            }
            if (x[0] === 'Mursten') {
                Mursten[1] = Mursten[1]+x[1]
            }        
        }
        newArray.push(Beton) 
        newArray.push(Træ)
        newArray.push(Skifer)
        newArray.push(Tegl)
        newArray.push(Isolering)
        newArray.push(Pap)
        newArray.push(Mursten)
        return newArray
    }

    removeMissing(array) {
        const newArray = []
        for (var x of array) {
            if (x[1] !== 0) {
                newArray.push(x)
            }
        } return newArray
    }

    getLabels(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[0])
        } return newArray
    }

    getData(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[1])
        } return newArray
    }

    render() {

         //-------------------------------------VARIABLES-----------------------------------------------
         //#region
         const area = this.props.building.area
         const floors = this.props.building.floors
         const totalArea = area*floors
         const perimeter = Math.floor(Math.sqrt(area)*4)
         const wallArea = perimeter*(floors*2.5)
         const roofArea = Math.floor(area*1.1)
         const windowArea = Math.floor(wallArea*0.15)
         //#endregion
         
         //-----------------------------------MATERIAL AMOUNTS------------------------------------------
         //#region
         const wallMaterialAmount = this.props.materials.wall.map((item) => <li>{item[0]}: {(item[1]*wallArea*0.5/100)}m3</li>)
         const wallMaterialAmount2 = this.props.materials.wall.map((item) => [item[0],item[1]*wallArea*0.5/100])

         const roofMaterialAmount = this.props.materials.roof.map((item) => <li>{item[0]}: {(item[1]*roofArea*0.5/100)}m3</li>)
         const roofMaterialAmount2 = this.props.materials.roof.map((item) => [item[0],item[1]*roofArea*0.5/100])

         const floorMaterialAmount = this.props.materials.floor.map((item) => <li>{item[0]}: {(item[1]*totalArea*0.5/100)}m3</li>)
         const floorMaterialAmount2 = this.props.materials.floor.map((item) => [item[0],item[1]*totalArea*0.5/100])

         const foundationMaterialAmount = this.props.materials.foundation.map((item) => <li>{item[0]}: {(item[1]*area*0.5/100)}m3</li>)
         const foundationMaterialAmount2 = this.props.materials.foundation.map((item) => [item[0],item[1]*area*0.5/100])

         const merged = [].concat.apply([], [wallMaterialAmount2,roofMaterialAmount2,floorMaterialAmount2,foundationMaterialAmount2]);
         const mergedMaterials = this.removeMissing(this.sumMaterials(merged))

         const mergedMaterialAmount = mergedMaterials.map((item) => <li>{item[0]}: {item[1]}m3</li>)
         //#endregion

         //---------------------------------------CHART-------------------------------------------------
         //#region 
         const chartData = this.getData(mergedMaterials)
         console.log(chartData)
         const chartLabels = this.getLabels(mergedMaterials)
         console.log(chartLabels)

         const data = {
            labels: chartLabels,
            datasets: [
              {
                label: '',
                data: chartData,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(255, 159, 64, 1)',
                ],
                borderWidth: 1,
              },
            ],
          }
          
          const options = {
            scales: {
              yAxes: [
                {
                  ticks: {
                    beginAtZero: true,
                  },
                },
              ],
            },
          }
        //#endregion


        return(
            <div>
                <h1 className='ResultHeader'>Resultater</h1>
                <div className='ResultContainer'>
                    <div className='aResult'>
                        <p>Ydervægge</p>
                        {this.props.materials.wallS}
                    </div>
                    <div className='aResult'>
                        <p>Tag</p>
                        {this.props.materials.roofS}
                    </div>
                    <div className='aResult'>
                        <p>Gulve</p>
                        {this.props.materials.floorS}
                    </div>
                    <div className='aResult'>
                        <p>Fundament</p>
                        {this.props.materials.foundationS}
                    </div>
                </div>
                <div className='SumContainer'>
                    <div className='SumResult'>
                        <p>Samlet</p>
                        {mergedMaterialAmount}
                    </div>
                </div>
                <div className='ChartContainer'>
                    <div className='BarContainer'>
                        <Bar data={data} options={{ maintainAspectRatio: false }} width={400} height={400}/>
                    </div>
                </div>
            </div>
        )
    }

}

export default ResultsSimple