import React from 'react';
import './SearchBar.css';
import { ReactSearchAutocomplete } from 'react-search-autocomplete'
import items from '../../items/items'
import { db } from "../../util/firebase";

class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {addressSelect: '',
                  addressSelected: false,
                  addressInfo: []}

    this.handleOnAddressSelect = this.handleOnAddressSelect.bind(this)
    this.handleOnSelect = this.handleOnSelect.bind(this);
    this.handleOnFocus = this.handleOnFocus.bind(this);
  }

  handleOnAddressSelect = (item) => {
    const address = item.name;
    db.ref(address).on("value", snapshot => {
        let allInfo = [];
        snapshot.forEach(snap => {
          allInfo.push(snap.val());
        });
        this.setState({ addressInfo: allInfo });
        console.log(this.state.addressInfo)
      });
  }

  handleOnSelect = () => {
    const address = this.state.addressInfo[7];
    const area = this.state.addressInfo[0];
    const floors = this.state.addressInfo[3];
    const built = this.state.addressInfo[1];
    const selectAddress = true;
    const location = this.state.addressInfo[5];
    const goBack = false;
    const floorHeight = 2.5;
    const roofType = 'Saddel';
    const foundationType = 'Linje';
    const energy = this.state.addressInfo[2];
    this.props.onChange(address,area,floors,built,selectAddress,location,goBack,floorHeight,roofType,foundationType,energy)
  }

  handleOnFocus = () => {
    console.log("Focused");
  }

  render() {
    if (this.props.building.goBack === true) {
    return (
      <div className='SearchPage'>
        <div className='PageHeader'>
          <h1>Bygningspas</h1>
        </div>
        <div className="SearchBar">
          <header className="SearchHeader">
              <div className="SearchBar-fields" style={{ width: 200, margin: 20 }}>
                <ReactSearchAutocomplete
                placeholder="Adresse" 
                items = {items}
                onSelect={this.handleOnAddressSelect}
                onFocus={this.handleOnFocus}
                styling={{ zIndex: 2 }} // To display it on top of the search box below
                autoFocus
              />
              <a className='buttonSearch' onClick={this.handleOnSelect}>Søg</a>
            </div>
          </header>
        </div>
      </div>
      
    );
    } else {
      return(
        <div></div>
      )
    }
  }
}

export default SearchBar;